.question div.alert,
.question div.warning,
.question div.info,
.question div.success {
  display: none;
}

.alert.question div.alert,
.warning.question div.warning,
.info.question div.info,
.success.question div.success {
  display: block;
}

.alert.question select {
  border-color: #ee6b6b;
  color: #8b1010;
}

.warning.question select {
  border-color: #f3b25d;
  color: #89520a;
}

.info.question select {
  border-color: #c5beb7;
  color: #6a6055;
}

.success.question select {
  border-color: #24f462;
  color: #04571d;
}

.required .required-holder {
  display: inline-flex;
}

select {
  background: unset !important;
  appearance: initial !important;
}

.switch {
  margin-bottom: 0;
}

.switch-value {
  margin-left: 0.5rem;
}

.question {
  display: flex;
  flex-direction: column;
}

.label-holder {
  flex: 1 0 auto;
}

.input-holder {
  flex: 1 0 auto;
}

.message-holder {
  flex: 1 0 auto;
}

.message-holder div {
  border-top-width: 5px;
  border-top-style: solid;
  padding: 0.25rem;
}

.hint-holder {
  flex: 1 0 auto;
}

